import { TemplateRef } from '@angular/core';

export type DscTableColumnAlign = 'left' | 'center' | 'right';

export type DscTableFixed = 'rowTop' | 'rowBottom' | 'columnLeft' | 'columnRight' | 'header' | undefined;

export type DscTableOrder = 'asc' | 'desc' | '';

export class DscTableColumn {
  private _title?: string;

  private _property!: string;

  private _value: Function;

  private _width?: number;

  private _headerAlign: DscTableColumnAlign;

  private _bodyCellAlign: DscTableColumnAlign;

  private _sortable = false;

  private _sortID!: string;

  private _sortableActive = false;

  private _showUnsortedIcon = true;

  private _currentDirection = '';

  private _footerTitle?: string;

  private _footerCalc?: Function;

  private _fixed?: DscTableFixed;

  private _headerFixed?: boolean;

  private _headerTooltip?: string;
  private _headerTooltipVariant?: boolean;

  private _template?: TemplateRef<any>;

  constructor(
    public title: string | undefined,
    public property: string,
    public value: Function,
    public width: number | undefined,
    public headerAlign: DscTableColumnAlign,
    public bodyCellAlign: DscTableColumnAlign,
    public sortable: boolean,
    public sortID: string,
    public sortableActive: boolean,
    public showUnsortedIcon: boolean,
    public currentDirection: DscTableOrder,
    public footerTitle: string,
    public footerCalc: Function,
    public fixed: DscTableFixed,
    public headerFixed: boolean,
    public headerTooltip: string,
    public headerTooltipVariant: boolean,
    public template: TemplateRef<any> | undefined
  ) {
    this._title = title;
    this._property = property;
    this._value = value;
    this._width = width;
    this._headerAlign = headerAlign;
    this._bodyCellAlign = bodyCellAlign;
    this._sortable = sortable;
    this._sortID = sortID;
    this._sortableActive = sortableActive;
    this._showUnsortedIcon = showUnsortedIcon;
    this._currentDirection = currentDirection;
    this._footerTitle = footerTitle;
    this._footerCalc = footerCalc;
    this._fixed = fixed;
    this._headerFixed = headerFixed;
    this._headerTooltip = headerTooltip;
    this._headerTooltipVariant = headerTooltipVariant;
    this._template = template;
  }
}
