import {
  AfterViewInit,
  Component,
  ContentChild,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { MatTableDataSource, MatTableModule } from '@angular/material/table';
import { CdkTableModule } from '@angular/cdk/table';
import { AsyncPipe, NgClass, NgForOf, NgIf, NgSwitch, NgSwitchCase, NgTemplateOutlet } from '@angular/common';
import { MatSort, MatSortModule, Sort } from '@angular/material/sort';
import { PageEvent } from '@angular/material/paginator';
import { SelectionModel } from '@angular/cdk/collections';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';
import { FormsModule } from '@angular/forms';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { MatIconModule } from '@angular/material/icon';

import { DscPaginatorComponent } from 'sidsc-components/dsc-paginator';
import { DscCheckboxComponent } from 'sidsc-components/dsc-checkbox';
import { DscRadioButtonComponent } from 'sidsc-components/dsc-radio-button';
import { DscButtonComponent } from 'sidsc-components/dsc-button';

import { DscTableColumn } from './shared/dsc-table-column';
import { DscTooltipModule } from 'sidsc-components/dsc-tooltip';

export type DscTableSelectorType = 'checkbox' | 'radio-button';

@Component({
  selector: 'dsc-table',
  standalone: true,
  encapsulation: ViewEncapsulation.None,
  imports: [
    MatTableModule,
    CdkTableModule,
    NgIf,
    NgTemplateOutlet,
    DscPaginatorComponent,
    MatSortModule,
    NgForOf,
    DscCheckboxComponent,
    NgSwitch,
    NgSwitchCase,
    DscRadioButtonComponent,
    FormsModule,
    DscButtonComponent,
    MatIconModule,
    DscTooltipModule,
    NgClass,
    AsyncPipe
  ],
  templateUrl: './dsc-table.component.html',
  styleUrls: ['./dsc-table.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed, void', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class DscTableComponent<T> implements AfterViewInit, OnChanges {

  @ViewChild(MatSort, { static: true }) matSort!: MatSort;

  @ViewChild(DscPaginatorComponent) dscPaginator!: DscPaginatorComponent;

  @ContentChild('customActionButtons', { static: true }) customActionButtons!: TemplateRef<any>;

  @Input() columns: DscTableColumn[] = [];

  @Input() data: T[] = [];

  @Input() paginatorPageIndex = 0;

  @Input() paginatorLength = 0;

  @Input() paginatorPageSize = 10;

  @Input() pageSizeOptions = [5, 10, 25];

  @Input() selectorType?: DscTableSelectorType;

  @Input() expandedDetailTemplate!: TemplateRef<any>;

  @Input() tableMaxHeight?: string;

  @Input() tableRowHeight?: string;

  @Input() selectedItems: T[] = [];

  @Input() showUnsortedIcon: boolean = true;

  @Input() footerFixed: boolean = false;

  @Input()
  get emptyMessageData(): string{
    return this._emptyMessageData;
  }

  set emptyMessageData(value: string){
    this._emptyMessageData = value;
  }

  private _emptyMessageData: string = 'Sem dados';

  @Input()
  get paginator(): boolean {
    return this._paginator;
  }

  set paginator(value: BooleanInput) {
    this._paginator = coerceBooleanProperty(value);
  }

  private _paginator: boolean = true;

  @Input()
  get backendPagination(): boolean {
    return this._backendPagination;
  }

  set backendPagination(value: BooleanInput) {
    this._backendPagination = coerceBooleanProperty(value);
  }

  private _backendPagination: boolean = false;

  @Input()
  get emitPageEventAfterView(): boolean {
    return this._emitPageEventAfterView;
  }

  set emitPageEventAfterView(value: BooleanInput) {
    this._emitPageEventAfterView = coerceBooleanProperty(value);
  }

  private _emitPageEventAfterView: boolean = false;

  @Input()
  get expandable(): boolean {
    return this._expandable;
  }

  set expandable(value: BooleanInput) {
    this._expandable = coerceBooleanProperty(value);
  }

  private _expandable: boolean = false;

  @Input()
  get showFirstLastButtons(): boolean {
    return this._showFirstLastButtons;
  }

  set showFirstLastButtons(value: BooleanInput) {
    this._showFirstLastButtons = coerceBooleanProperty(value);
  }

  @Input()
  get hidePageSize(): boolean {
    return this._hidePageSize;
  }

  set hidePageSize(value: BooleanInput) {
    this._hidePageSize = coerceBooleanProperty(value);
  }

  @Input()
  get showPageSizeOptions(): boolean {
    return this._showPageSizeOptions;
  }

  set showPageSizeOptions(value: BooleanInput) {
    this._showPageSizeOptions = coerceBooleanProperty(value);
  }

  @Input()
  get hidePaginatorRangeLabel(): boolean {
    return this._hidePaginatorRangeLabel;
  }

  set hidePaginatorRangeLabel(value: BooleanInput) {
    this._hidePaginatorRangeLabel = coerceBooleanProperty(value);
  }

  @Input()
  get disabledPaginator(): boolean {
    return this._disabledPaginator;
  }

  set disabledPaginator(value: BooleanInput) {
    this._disabledPaginator = coerceBooleanProperty(value);
  }

  @Input()
  get expandMultiple(): boolean {
    return this._expandMultiple;
  }

  set expandMultiple(value: BooleanInput) {
    this._expandMultiple = coerceBooleanProperty(value);
  }

  private _hidePaginatorRangeLabel = false;
  private _showFirstLastButtons = true;
  private _hidePageSize = false;
  private _showPageSizeOptions = true;
  private _disabledPaginator = false;
  private _expandMultiple = false;

  private lastSort?: Sort;

  @Output() page: EventEmitter<PageEvent> = new EventEmitter();

  @Output() selectionChange: EventEmitter<T[]> = new EventEmitter<T[]>();

  @Output() checkboxChange: EventEmitter<T[]> = new EventEmitter<T[]>();

  @Output() radioButtonChange: EventEmitter<T> = new EventEmitter<T>();

  @Output() sortChange: EventEmitter<Sort> = new EventEmitter<Sort>();

  @Output() rowExpanded = new EventEmitter<any>();

  selection: SelectionModel<T> = new SelectionModel<T>(true, []);

  radioButtonSelected?: T;

  displayedColumns: any[] = [];

  dataSource!: MatTableDataSource<T>;

  expandedElement!: T;

  expandedElements: any[] = [];

  currentDirection: 'asc' | 'desc' | '' = '';

  checkboxAllLabel = 'Selecionar todos os registros';

  ngAfterViewInit(): void {
    if (this.paginator && this.dataSource && !this.backendPagination) this.dataSource.paginator = this.dscPaginator;
    if (this.dataSource) this.dataSource.sort = this.matSort;

    this.dataSource.sortingDataAccessor = (item: any, property: string) => {
      const value = item[property];

      if(typeof value === 'string' && /^\d{2}\/\d{2}\/\d{4}$/.test(value)) {
        const [day, month, year] = value.split('/').map(Number);
        return new Date(year, month -1, day);
      }

      if(this.isNumeric(value)) {
        return Number(value);
      }

      return value;
    }

    setTimeout(() => {
      if (this.paginator && this.backendPagination && this.emitPageEventAfterView) {
        this.onPage({
          pageSize: this.paginatorPageSize,
          pageIndex: 0,
          length: 0,
          previousPageIndex: 0
        });
      }
    });

    this.sortChange.subscribe((sort: Sort) => {
      this.onSortChange(sort);
    })
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['data']) this._tableInit();
    if (changes['selectedItems']) this._setInitialSelection();
  }

  private _setInitialSelection(): void {
    if (this.selectorType === 'checkbox' && this.selectedItems?.length) {
      this.selection.clear();
      this.selectedItems.forEach(item => this.selection.select(item));
    }
  }

  private isNumeric(value: any): boolean {
    return value !== null && value !== '' && !isNaN(value) && !isNaN(parseFloat(value));
  }

  private _tableInit(): void {
    this._createDataSource();
    this._setColumns();
  }

  private _createDataSource(): void {
    this.dataSource = new MatTableDataSource(this.data);
    this.dataSource.sort = this.matSort;
    if (this.paginator && !this.backendPagination) this.dataSource.paginator = this.dscPaginator;
  }

  private _setColumns() {
    this.displayedColumns = this.columns.map((tableColumn: DscTableColumn) => tableColumn.property);

    switch (this.selectorType) {
      case 'checkbox':
        this.displayedColumns = ['checkbox', ...this.displayedColumns];
        break;
      case 'radio-button':
        this.displayedColumns = ['radio-button', ...this.displayedColumns];
        break;
    }

    if (this.expandable) this.displayedColumns = [...this.displayedColumns, 'expand'];
  }

  private _getCurrentPageData(): T[] {
    if(!this.dscPaginator || !this.dataSource) return [];

    const startIndex = this.dscPaginator?.pageIndex * this.dscPaginator?.pageSize;
    const endIndex = startIndex + this.dscPaginator?.pageSize;

    return this.dataSource.data
      .slice(startIndex, endIndex)
      .filter((value: any) => !value['disabled']);
  }

  onPage(pageEvent: PageEvent): void {
    this.page.emit(pageEvent);
  }

  onSortChange(sort: Sort): void {
    this.columns.forEach(column => {
      if(column.property === sort.active) {
        column.sortableActive = true;
        column.currentDirection = sort.direction;
      } else {
        column.sortableActive = false;
        column.currentDirection = '';
      }
    });

    if(!this.lastSort 
      || this.lastSort.active !== sort.active 
      || this.lastSort.direction !== sort.direction) {
        this.lastSort = sort;
        this.sortChange.emit(sort);
    }
   
  }

  getSortIcon(column: any): string {
    if(!column.sortable) return '';

    if(column.sortableActive) {
      switch(column.currentDirection) {
        case 'asc':
          return 'arrow_upward';
        case 'desc':
          return 'arrow_downward';
        case '':
          return column.showUnsortedIcon ? 'swap_vert' : '';
      }
    }

    return column.showUnsortedIcon ? 'swap_vert': '';
  }

  getFooterValue(col: DscTableColumn): any {
    if(col.footerCalc && typeof col.footerCalc === 'function') {
      return col.footerCalc(this.data)
    }

    return '';
  }

  get hasFooter(): boolean {
    return this.columns?.some(c => c.footerTitle || c.footerCalc);
  }

  isExpanded(row: any): boolean {
    return this.expandMultiple ? this.expandedElements.includes(row) : this.expandedElement === row;
  }

  isAllSelected(): boolean {
    const pageData = this._getCurrentPageData();
    return pageData.every(row => this.selection.isSelected(row));
  }

  toggleAllRows(): void {
    const pageData = this._getCurrentPageData();
    const allSelected = pageData.every(row => this.selection.isSelected(row));

    if(allSelected) {
      pageData.forEach(row => this.selection.deselect(row));
    } else {
      pageData.forEach(row => this.selection.select(row));
    }

    this.onSelectionChange();
    this.checkboxChange.emit(this.selection.selected);
  }

  checkboxLabel(row?: T): string {
    return row
      ? `${this.isAllSelected() ? 'Desselecionar' : 'Selecionar'} todos os registros` 
      : this.checkboxAllLabel;
  }

  onSelectionChange(): void {
    this.selectionChange.emit(Array.from(this.selection.selected));
  }

  onSelectChange($event: any, row: T): void {
    if ($event) this.selection.toggle(row);
    this.onSelectionChange();
    this.checkboxChange.emit(this.selection.selected);
  }

  onRadioButtonChange(row: T): void {
    this.radioButtonChange.emit(row);
  }

  onClick(row: any, $event: any): void {
    $event.stopPropagation();
    this.rowExpanded.emit(row);

    if (!row.disabled) {
      if(this.expandMultiple) {
        const index = this.expandedElements.findIndex(r => r === row);

        if(index >= 0) {
          this.expandedElements.splice(index, 1);
        } else {
          this.expandedElements.push(row);
        }
      } else {
        this.expandedElement = this.expandedElement === row ? null : row;
      }

    }
  }
}
