import { TemplateRef } from '@angular/core';
import { coerceNumberProperty, NumberInput } from '@angular/cdk/coercion';

import { DscTableColumn, DscTableColumnAlign, DscTableFixed, DscTableOrder } from './dsc-table-column';
export class DscTableColumnBuilder {
  private _title?: string;

  private _property!: string;

  private _value: any;

  private _width?: number;

  private _headerAlign: 'left' | 'center' | 'right' = 'left';

  private _bodyCellAlign: 'left' | 'center' | 'right' = 'left';

  private _sortable = false;

  private _sortID!: string;

  private _sortableActive = false;

  private _showUnsortedIcon = true;

  private _currentDirection: 'asc' | 'desc' | '' = '';

  private _footerTitle!: string;

  private _footerCalc?: any;

  private _fixed: 'rowTop' | 'rowBottom' | 'columnLeft' | 'columnRight' | 'header' | undefined;

  private _headerFixed = false;

  private _headerTooltip!: string;

  private _headerTooltipVariant!: boolean;

  private _template?: TemplateRef<any>;

  public static instance(): DscTableColumnBuilder {
    return new DscTableColumnBuilder();
  }

  public title(title: string): DscTableColumnBuilder {
    this._title = title;
    return this;
  }

  public property(property: string): DscTableColumnBuilder {
    this._property = property;
    return this;
  }

  public value(value: any): DscTableColumnBuilder {
    this._value = value;
    return this;
  }

  public width(width: NumberInput): DscTableColumnBuilder {
    this._width = coerceNumberProperty(width);
    return this;
  }

  public headerAlign(headerAlign: DscTableColumnAlign): DscTableColumnBuilder {
    this._headerAlign = headerAlign;
    return this;
  }

  public bodyCellAlign(bodyCellAlign: DscTableColumnAlign): DscTableColumnBuilder {
    this._bodyCellAlign = bodyCellAlign;
    return this;
  }

  public sortable(sortable: boolean): DscTableColumnBuilder {
    this._sortable = sortable;
    return this;
  }

  public sortID(sortID: string): DscTableColumnBuilder {
    this._sortID = sortID;
    return this;
  }

  public sortableActive(sortableActive: boolean): DscTableColumnBuilder {
    this._sortableActive = sortableActive;
    return this;
  }
  
  public showUnsortedIcon(showUnsortedIcon: boolean): DscTableColumnBuilder {
    this._showUnsortedIcon = showUnsortedIcon;
    return this;
  }

  public currentDirection(currentDirection: DscTableOrder): DscTableColumnBuilder {
    this._currentDirection = currentDirection;
    return this;
  }

  public footerTitle(footerTitle: string): DscTableColumnBuilder {
    this._footerTitle = footerTitle;
    return this;
  }

  public footerCalc(footerCalc: any): DscTableColumnBuilder {
    this._footerCalc = footerCalc;
    return this;
  }

  public fixed(fixed: DscTableFixed): DscTableColumnBuilder {
    this._fixed = fixed;
    return this;
  }

  public headerFixed(headerFixed: boolean): DscTableColumnBuilder {
    this._headerFixed = headerFixed;
    return this;
  }

  public headerTooltip(headerTooltip: string): DscTableColumnBuilder {
    this._headerTooltip = headerTooltip;
    return this;
  }

  public headerTooltipVariant(headerTooltipVariant: boolean): DscTableColumnBuilder {
    this._headerTooltipVariant = headerTooltipVariant;
    return this;
  }

  public template(template: TemplateRef<any>): DscTableColumnBuilder {
    this._template = template;
    return this;
  }

  public build(): DscTableColumn {
    return new DscTableColumn(
      this._title,
      this._property,
      this._value,
      this._width,
      this._headerAlign,
      this._bodyCellAlign,
      this._sortable,
      this._sortID,
      this._sortableActive,
      this._showUnsortedIcon,
      this._currentDirection,
      this._footerTitle,
      this._footerCalc,
      this._fixed,
      this._headerFixed,
      this._headerTooltip,
      this._headerTooltipVariant,
      this._template
    );
  }
}
